<template>
	
		<div>
			<h2>--{{title}}</h2>
			<button @click="run(123)"></button>
			<button @click="runParent()"></button>
			<button @click="getParent()"></button>
			<button @click="toEvent()">News</button>
		</div>
	
</template>

<script>
	import VueEvent from"../model/VueEvent.js";
	
	export default {
		data() {
			return {
				Hmsg:"headerData",
				childData:"child"
			};
			
		},
		methods:{
			runParent(){
				alert(this.title) //this.home.title
			},
			runChild(){
				alert("")
			},
			getParent(){
				alert(this.$parent.msg)
			},
			toEvent(){
				VueEvent.$emit("to-news",this.Hmsg)
			}
				
		},
		
		
		props:['title','run'],
	}
</script>

<style>

</style>



