<template>
	
		<div id="home">
			<h1>1.2.props</h1>
			<h2 >----{{msg}}</h2>
			<button @click="run()"></button>
			<v-head :title="title" :run="run" :home='this' ref='head'></v-head>
			
			<br />
			<h5>
			ref="header";     this.$refs.header //
			   this.$parent.msg/title .run()
			</h5>
			<button @click="childData()"></button>
			<v-news></v-news>
			
			
			
		</div>
	
</template>

<script>
	import Header from "../compnents/Header";
	import News from "../compnents/News";

	export default {
		data() {
			return {
			msg:"",
			title:"",
			
			};
		}, methods:{
			run(data){
				alert(this.msg+data)
			},
			childData(){
			//	alert(this.$refs.head.childData) 
			 this.$refs.head.runChild()
			}
		},
		components:{
			'v-head':Header,
			'v-news':News,
		}
	}
</script>

<style lag="scss" >
 
 #home{
	 h2{
	 color: red
	 } 
 }
	 
</style>



