<template>
  <div id="app">
    <img src="./assets/logo.png">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
   <br />
	 <br /><br />
		<div>ddd</div>
		<h1>{{obj.name}}</h1>
		<br />
		<ul>
			<li v-for="item in list">
				{{item}}
			</li>
		</ul>
		<ul>
			<li v-for="item in list1">
				{{item.title}}
			</li>
		</ul>
		<ul>
			<li v-for="item in list2">
				{{item.cate}}
				<ol>
					<li v-for="news in item.list">
						{{news.title}}
					</li>
				</ol>
			</li>
		</ul>
		<!-- <img src="assets/logo.png" /> -->
		<img v-bind:src="url" /> <!-- -->
		<img :src="url" />  <!-- -->
		<div v-bind:title="title"></div>
		<div v-html="h">html</div>
		<div v-text="msg"></div>
		<div v-bind:class="{'red22':flag,'blue':!flag}">class</div>
		<ul>
			<li v-for="(item,key) in list" :class="{'blue':key==0}">
				{{key}}---{{item}}
			</li>
		</ul>
		<div v-bind:style="{'width':boxwidth+'px','height':boxhight+'px','background':boxground}"></div>
		<div>{{msg}}</div>
		<input type="text" v-model="msg" />
		<button v-on:click="getmsg()"></button>
		<button v-on:click="setmsg()"></button>
		<br />	<br />	<br />
		<input type="text" ref="user" />
		<div ref="box"></div>
		<button v-on:click="getInputValue()"></button>
		<button @click="getInputValue()"></button>
		<ul>
			<li v-for="item in listRq">
				{{item}}
			</li>
		</ul>
		<button @click="requestData()"></button>
		<button @click="dilteData(333)"></button>
		<button data-aid="zdy" @click="eventFn($event)"></button> <!-- -->
		 <br />
		 <br />
		
		<input type="text" v-model="todu" @keydown="addFn($event)" />	
		<button @click="addText()"></button>
	
	<br />
	<h2>...</h2>
	<ul>
		<li v-for="(item,key) in listTodu" v-if="!item.checked">
				<input type="checkbox" v-model="item.checked" @change="saveList()"/> {{item.title}} ------<button @click="remvo(key)"></button>
		</li>
	</ul>
	<h2>...</h2>
	<ul>
		<li v-for="(item,key) in listTodu" v-if="item.checked">
				<input type="checkbox" v-model="item.checked" @change="saveList()"/> {{item.title}} <button @click="remvo(key)"></button>
		</li>
	</ul>
	<hr><br/><br/><br/><br/><br/>
	
	<!--  -->
	<v-home></v-home>
	
	<h1></h1>
	<v-life v-if="flag1"></v-life>
	
	<button @click="flag1=!flag1"></button>
	<br /><br />
	<button @click="getData()"></button>
	
	<hr />
	<br />
	<router-link to="/RouterA">Go to Foo</router-link>
	<router-link to="/RouterB">Go to Bar</router-link>

	
	<!--  -->
	<router-view></router-view>
	<br /><br />
	</div>
	

</template>

<script>
import storage from"./model/storage.js"; //js
import Home from "./compnents/Home.vue";
import Life from "./compnents/Life.vue";

 //1.       2.    3.
 
/*1

    1

       routes: [
        //  
        { path: '/user/:id', component: User }
      ]


    2

      this.$route.params


*/ 
 
export default{
	name:"app",
	data (){
		return{
			msg:"to you Vue",
			obj:{
			name:"lili"
			},
			url:"",
			title:"",
			flag:false,
			flag1:true,
			boxwidth:200,
			boxhight:200,
			boxground:"red",
			h:"<h2><h2/>",
			list:["aa","bb","cc"],
			list1:[{"title":"111"},
							{"title":"222"},
							{"title":"333"}
					],
			list2:[
							{"cate":"1",
								"list":[{"title":"111"},
												{"title":"xinwei222"}
												]

							},
							{
								"cate":"2",
									"list":[
										{"title":"111a"},
										{"title":"xinwei222b"}
											]
							}
					
			],
			listRq:[],
			
			todu:"",
			listTodu:[
				{"title":"ionc",
				checked:false},
				{"title":"ionc",
				checked:true}
			]
			
		}
	},methods:{//
		getmsg(){
		/* 	alert("vue") */
		alert(this.msg) //data
		},
		setmsg(){
			this.msg=""
		},getInputValue(){
			// refDOM
		console.info(this.$refs.user)	;
		//value
			console.info(this.$refs.user.value);
			this.$refs.box.style.background="red";
		},requestData(){
			for (var i=0;i<5; i++){
				this.listRq.push(""+i+"")
			};
		},dilteData(ss){
			alert(ss);
		},eventFn(e){
			console.log(e);
			//e.srcElement DOM
			e.srcElement.style.background="red";
			console.log(e.srcElement.dataset.aid);
			
		},addText(){
			//list
		
			this.listTodu.push({"title":this.todu,
													checked:false
			});
			console.log(this.listTodu)
			this.todu="";
			storage.set("list",this.listTodu);
		//	localStorage.setItem("list",JSON.stringify(this.listTodu))
		} ,remvo(key){
			
			this.listTodu.splice(key,1);
			
			localStorage.setItem("list",JSON.stringify(this.listTodu))
		},addFn(e){
			if(e.keyCode==13){
				this.listTodu.push({
					title:this.todu,
					checked:false
				});
				this.todu="";
				localStorage.setItem("list",JSON.stringify(this.listTodu))
			}
			
		},saveList(){
			localStorage.setItem("list",JSON.stringify(this.listTodu))
		},getData(){ //
		var api="";
		/* 1.vue-resource
		 this.$http.get(api).then(function(response){
			console.log(response)
		},function(err){
			console.log(err)
		}) 
		/
		 this
		
		this.$http.get(api).then((response)=>{
			this.list=response.body.result
		})
		
		2.axios
		Axios.get(api).then((response)=>{
			
		}).catch((err)=>{
			
		})
		
		
		
		*/
		}
		
		
	},mounted(){ //  
		var list= JSON.parse(localStorage.getItem("list"));   
		if(list){
			this.listTodu=list
		};
		
		getData();
		
		
	}, components:{
		"v-home":Home,
		"v-life":Life,
	},
	
}
</script>

<style lang="scss">

.red22{
	color: red;
}
.blue{
	color: aqua;
	/* background: #00FFFF; */
}
.bg{
	 background: #00FFFF;
}
</style>



