<template>
	
		<div id="life">
			<h3>---{{msg}}</h3>
			<button @click="run()"></button>
		</div>
	
</template>

<script>
	export default {
		data() {
			return {
			msg:""
			};
		}, methods:{
			run(){
				this.msg=""
			}
		},
		beforeCreate() {
		console.info("")	//
		},
		created() {
			console.info("")
			//
		},
		beforeMount() {
			console.info("")
			//
		},
		mounted() {
			console.info("")
			//
		},
		beforeUpdate() {
			console.info("")
			//
		},
		updated() {
			console.info("")
		
		},
		beforeDestroy() {
			console.info("")
			//
		},destroyed() {
			console.info("")
			//
		}
	}
</script>

